	static AddToCart(email,orderPK,productID,discountRate)
	{
		let token = Utils.getUserToken();
		let para = {
			ParameterNames: 'email|orderPK|productID|discountRate',
			Parameters: [email,orderPK,productID,discountRate]
		};

		Object.assign(token, para);

		let url =  Utils.getBaseUrl() + 'FacadeService/GetBizOrderResult/AddToCart?access_token=C250A210-A970-4F02-A513-5757A90EEE5B';
		return axios.post(url, token);
	}

