<template>
<div class="page">
	<div class="content">
		<h3>Welcome {{user}}</h3>
	</div>
	<mt-tabbar v-model="selected" fixed>
		<mt-tab-item id="product"> 
			<img slot="icon" src="../assets/product.png">
			Menu
		</mt-tab-item>
		<mt-tab-item id="cart">
			<img slot="icon" src="../assets/cart.png">
			Cart
		</mt-tab-item>
		<mt-tab-item id="history">
			<img slot="icon" src="../assets/history.png">
			History
		</mt-tab-item>
		<mt-tab-item id="profile">
			<img slot="icon" src="../assets/profile.png">
			Profile
		</mt-tab-item>
		<mt-tab-item id="logout">
			<img slot="icon" src="../assets/logout.png">
			Logout
		</mt-tab-item>
	</mt-tabbar>
</div>
</template>

<script>
import Utils from '../utils/index.js';

export default {
	name: 'Dashboard',
	data() {
		return {
			selected: '',
			user: ''
		}
	},
	created(){
		let customer = Utils.getCustomer();
		this.user = customer.Email;
	},
	watch:{
		selected: function (val, oldVal) {
			if(val == "product")
			{
				this.$router.push({ name: 'ProductList' });
			}
			else if(val == "cart")
			{
				this.$router.push({ name: 'Cart' });
			}
			else if(val == "history")
			{
				this.$router.push({ name: 'HistoryList' });
			}
			else if(val == "profile")
			{
				this.$router.push({ name: 'Profile' });
			}
			else if(val == "logout")
			{
				Utils.clearUserToken();
				this.$router.push({ name: 'Login' });
			}
		}
	}
}

</script>

<style scoped lang="scss">
	.page{
		background: url('../assets/background.jpg') center center;
		background-size: cover;
	}
	.content{
		min-height:100vh;
		text-align: center;
		margin-bottom: 50px;
	}
	.mint-tabbar {
		background-color: #d2d2d2;
	}
</style>
