<template>
<div>
	<div class="page-header">
		<mt-header fixed title="Shopping Items Confirmation">
			<router-link :to="{name: 'ProductList'}" slot="left">
				<mt-button icon="back">Back</mt-button>
			</router-link>
		</mt-header>
	</div>

	<div class="page-body">
		<table style="width:100%;">
			<tbody>
			<tr v-for="item in cartItemList" :key="item.OrderItemPK">
				<td>{{item.Quantity}}</td>
				<td>{{item.ProductName}}</td>
				<td align="right">{{item.PaidPrice.toFixed(2)}}</td>
				<td align="center">{{item.Notes}}</td>
				<td>
					<mt-button @click="itemClick(item.OrderItemPK)" icon="more"></mt-button>
				</td>
			</tr>
			</tbody>
			<tfoot>
				<tr>
					<td align="right" colspan="3">{{totalPrice.toFixed(2)}}</td>
					<td colspan="2"></td>
				</tr>
			</tfoot>
		</table>

		<mt-button style="width:100%;" type="primary" @click="checkout">Proceed to Checkout</mt-button>
		<mt-button style="width:100%;" type="primary" @click="shopping">Continue Shopping</mt-button>

		<mt-actionsheet
			:actions="command"
			v-model="commandVisible"
			cancelText = "Cancel">
		</mt-actionsheet>
	</div>
</div>
</template>

<script>
import { Indicator, Toast, MessageBox  } from 'mint-ui';
import BizOrder from '../api/BizOrder.js'
import OrderItem from '../api/OrderItem.js'
import Utils from '../utils/index.js';

export default {
	name: 'Cart',
	data(){
		return {
			cartItemList:[],
			totalPrice:0,
			command: [],
			commandVisible: false,
			selectedItem: ''
		}
	},
	methods:{
		showNetworkError()
		{
			Toast({message: 'The Web Server may be too busy, please try again at a later time.', duration: 3000});
		},
		getCartItemByPK(OrderItemPK)
		{
			for(let i=0; i< this.cartItemList.Length;i++)
			{
				if(this.cartItemList[i].OrderItemPK == OrderItemPK)
				{
					return this.cartItemList[i];
				}
			}

			return null;
		},
		itemClick(pk){
			this.selectedItem = pk;
			this.commandVisible = true;
		},
		editItem()
		{
			this.$router.push({name: 'CartItem', params: {OrderItemPK: this.selectedItem}});
		},
		deleteItem()
		{
			let warning = 'Are you sure to delete this item ?';
			MessageBox.confirm(warning, 'Delete Warning', {confirmButtonText: 'OK', cancelButtonText: 'Cancel'}).then((action) => {
				Indicator.open();
				let pks = [this.selectedItem];
				OrderItem.DeleteByPKList(pks).then(res=>{
					Indicator.close();
					let customer = Utils.getCustomer();
					this.updateCart(customer.CustomerPK, customer.Email);
				}).catch(err=>{
					this.showNetworkError();
					console.log(err);
					Indicator.close();
				});
			}).catch(() => {});
		},
		shopping()
		{
			this.$router.push({ name: 'ProductList' });
		},
		checkout()
		{
			this.$router.push({ name: 'PayShipping' });
		},
		updateCart(CustomerPK, Email)
		{
			Indicator.open();
			BizOrder.GetShoppingCart(Email, CustomerPK).then(res=>{
				let order = res.data;

				this.totalPrice = 0;
				this.cartItemList = order.OrderItemList;
				for(let i=0; i< order.OrderItemList.length;i++)
				{
					this.totalPrice += order.OrderItemList[i].PaidPrice * order.OrderItemList[i].Quantity;
				}
				this.$store.commit('setCart', order);
				Indicator.close();
			}).catch(err=>{
				this.showNetworkError();
				console.log(err);
				Indicator.close();
			});
		}
	},
	mounted()
	{
		let customer = Utils.getCustomer();
		this.updateCart(customer.CustomerPK, customer.Email);

	    this.command.push({name: 'Edit', method: this.editItem});
		this.command.push({name: 'Delete', method: this.deleteItem});
	}
}
</script>

<style scoped>
	.page-header{
		margin-bottom: 40px;
	}
</style>
