<template>
<div>
    <div class="page-header">
		<mt-header fixed title="Product List">
			<router-link :to="{name: 'Dashboard'}" slot="left">
				<mt-button icon="back">Back</mt-button>
			</router-link>
		</mt-header>
	</div>

    <div class="page-body">
		<div style="height:120px;" v-for="item in list" :key="item.ProductPK">
			<img :src="'/static/images/'+item.ImageFile">
			<div style="float:right;">
				<mt-button @click="addToCartClick(item.ProductID)">+Add</mt-button>
			</div>

			<div style="float:right;">
				<p>{{item.ProductName}}</p>
				<p>{{item.Price.toFixed(2)}}</p>
			</div>
		</div>
	</div>
</div>
</template>

<script>
import { Indicator, Toast, MessageBox  } from 'mint-ui';
import Product from '../api/Product.js'
import BizOrder from '../api/BizOrder.js'
import Utils from '../utils/index.js';

export default {
	name: 'ProductList',
	data(){
		return {
			list:[]
		}
	},
	methods:{
		$message(data)
		{
			Toast({message: data.message, duration: 3000});
		},
		showNetworkError()
		{
			this.$message({message: 'The Web Server may be too busy, please try again at a later time.', type: 'error'});
		},
		addToCartClick(ProductID)
		{
			let cart = this.$store.state.cart;
			let customer = Utils.getCustomer();
			Indicator.open();
			BizOrder.AddToCart(customer.Email, cart.OrderPK, ProductID, customer.DiscountRate).then(res=>{
				Indicator.close();
				this.$router.push({name: 'Cart'});
			}).catch(err=>{
				this.showNetworkError();
				console.log(err);
				Indicator.close();
			});
		}
	},
	mounted()
	{
		this.list = this.$store.state.productList;
		if(this.list.length == 0)
		{
			Indicator.open();
			Product.GetAll().then(res=>{
				this.list = res.data;
				for(let i=0; i<this.list.length;i++)
				{
					if(this.list[i].ImageFile.length == 0)
					{
						this.list[i].ImageFile = 'default_card.jpg';
					}
				}
				this.$store.commit('setProductList', this.list);
				Indicator.close();
			}).catch(err=>{
				this.showNetworkError();
				console.log(err);
				Indicator.close();
			});
		}

		let order = this.$store.state.cart;
		if(Object.keys(order).length == 0)
		{
			Indicator.open();
			let customer = Utils.getCustomer();
			BizOrder.GetShoppingCart(customer.Email, customer.CustomerPK).then(res=>{
				this.$store.commit('setCart', res.data);
				Indicator.close();
			}).catch(err=>{
				this.showNetworkError();
				console.log(err);
				Indicator.close();
			});
		}
	}
}
</script>

<style scoped>
	.page-header{
		margin-bottom: 40px;
	}
</style>
