<template>
<div>
    <div class="page-header">
		<mt-header fixed :title="productName">
			<router-link :to="{name: 'Cart'}" slot="left">
				<mt-button icon="back">Back</mt-button>
			</router-link>
		</mt-header>
	</div>
	
	<div class="page-body">
		<mt-field label="Quantity" type="number" v-model="orderItem.Quantity"></mt-field>
		<mt-field label="Notes" type="text" v-model="orderItem.Notes"></mt-field>

		<div>
			<mt-button @click="cancelCartItemClick">Cancel</mt-button>
			<mt-button type="primary" @click="saveCartItemClick()">Save</mt-button>
		</div>
	</div>
</div>
</template>

<script>
import { Indicator, Toast, MessageBox  } from 'mint-ui';
import OrderItem from '../api/OrderItem.js'
import Utils from '../utils/index.js';

export default {
	name: 'CartItem',
	data(){
		return {
			productName: '',
			orderItem: {}
		}
	},
	methods:{
		showNetworkError()
		{
			Toast({message: 'The Web Server may be too busy, please try again at a later time.', duration: 3000});
		},
		getCartItemByPK(orderItemPK)
		{
			let cart = this.$store.state.cart;
			for(let i=0; i< cart.OrderItemList.length;i++)
			{
				if(cart.OrderItemList[i].OrderItemPK == orderItemPK)
				{
					return cart.OrderItemList[i];
				}
			}

			return null;
		},
		cancelCartItemClick()
		{
			this.$router.push({ name: 'Cart' });
		},
		saveCartItemClick()
		{
			Indicator.open();

			this.orderItem.IsDirty = true;
			this.orderItem.IsNew = false;
			this.orderItem.IsDeleted = false;
			OrderItem.Save(this.orderItem).then(res=>{
				Indicator.close();
				let entity = this.getCartItemByPK(this.orderItem.OrderItemPK);
				if(entity != null)
				{
					entity.Quantity = this.orderItem.Quantity;
					entity.Notes = this.orderItem.Notes;
				}
				this.$router.push({ name: 'Cart' });
			}).catch(err=>{
				this.showNetworkError();
				console.log(err);
				Indicator.close();
			});
		}
	},
	created()
	{
		let orderItemPK = this.$route.params.OrderItemPK;
		let entity = this.getCartItemByPK(orderItemPK);
		if(entity == null)
			return;
		
		this.orderItem = Object.assign({}, entity);
		this.productName = entity.ProductName;
	}
}
</script>

<style scoped>
	.page-header{
		margin-bottom: 40px;
	}
</style>
